<template>
  <div class="kanban_detail">
    <div class="box first">
      <div class="row">
        <div class="left">
          任务发起人:
        </div>
        <div class="right">
          {{taskDetail.sender}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          任务发起时间:
        </div>
        <div class="right">
          {{taskDetail.originator_time}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          任务开始时间:
        </div>
        <div class="right">
          {{taskDetail.task_start_time}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          任务结束时间:
        </div>
        <div class="right">
          {{taskDetail.task_end_time}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          优先级:
        </div>
        <div class="right">
          {{taskDetail.task_priority | priority}}
        </div>
      </div>
      <div class="hr"></div>
      <div class="row">
        <div class="left">
          任务类型:
        </div>
        <div class="right">
          {{taskDetail.task_type_id}}
        </div>
      </div>
    </div>
    <div class="box second">
      <div class="row">
        任务接收人:
      </div>
      <div class="hr"></div>
      <div class="row">
        {{taskDetail.sendee ? taskDetail.sendee.join('、') : '暂无'}}
      </div>
    </div>
    <div class="box third">
      <div class="row">
        任务描述:
      </div>
      <div class="hr"></div>
      <div class="row">
        {{taskDetail.task_describe}}
      </div>
    </div>
    <div class="box fourth" v-if="!showComplateBtn">
      <div class="row">
        完成情况:
      </div>
      <div class="table">
        <div class="trow odd">
          <div class="tcol">任务完成人</div>
          <div class="tcol">完成时间</div>
          <div class="tcol">任务反馈</div>
        </div>
        <div class="trow even" v-for="(item,k) in taskDetail.complateInfo" :key="item.id" :class="k%2===0?'even':'odd'">
          <div class="tcol">{{item.name}}</div>
          <div class="tcol">{{item.complateTime || "未完成"}}</div>
          <div class="tcol link" :class="{'disabled': !item.complateTime}" @click="showComplateDetail(item)">查看详情</div>
        </div>
      </div>
    </div>
    <div class="box fifth" v-if="showComplateBtn && taskDetail.receive_task_state!=='completed'">
      <div class="row">
        任务反馈:
      </div>
      <div class="hr"></div>
      <div class="row textarea_box">
        <textarea name="" id="" rows="10" v-model="feedBackContent" placeholder="请输入反馈内容"></textarea>
      </div>
    </div>
    <common-btn class="bottom_btn" v-if="showComplateBtn && taskDetail.receive_task_state!=='completed'" @click="commitTask">确认完成</common-btn>
    <div class="blank"></div>
    <my-dialog v-if="isFeedBackShow">
      <div class="feedback_box">
        <h3>任务反馈</h3>
        <div class="content">
          <p>{{nowFeedBack || '暂无'}}</p>
        </div>
        <common-btn @click="closeFeedBackDialog">关闭</common-btn>
      </div>
    </my-dialog>
  </div>
</template>

<script>
const PRIORITY = 'priority'
const INTERMEDIATE = 'intermediate'
const ELEMENTARY = 'elementary'
export default {
  name: 'KanbanDetail',
  data () {
    return {
      id: '',
      isFeedBackShow: false,
      nowFeedBack: '', // 显示反馈弹窗
      taskDetail: {},
      feedBackContent: ''
    }
  },
  methods: {
    showComplateDetail (item) {
      if (item.complateTime) {
        this.nowFeedBack = item.feedback
        this.isFeedBackShow = true
      }
    },
    closeFeedBackDialog () {
      this.nowFeedBack = ''
      this.isFeedBackShow = false
    },
    // 格式化时间
    formatDateTime (v) {
      return v && v.format('YY-MM-DD HH:mm:ss')
    },
    getTaskDataDetail (id) {
      this.$rpc.call('funenc_xa_station.work_kanban', 'get_kanban_by_id', [], {
        id: id
      }).then(res => {
        this.taskDetail = res
      }).catch(e => {
        console.log(e)
        alert('获取任务详情出错，请刷新')
      })
    },
    commitTask () {
      this.$rpc.call('funenc_xa_station.work_kanban', 'app_save_kanban_type', [], {
        taskid: this.id,
        feedBackContent: this.feedBackContent
      }).then(res => {
        if (res) {
          this.$router.push('/kanban')
        }
      }).catch(e => {
        console.log(e)
        alert('确认完成出错，请重试')
      })
    }
  },
  computed: {
    showComplateBtn () {
      return this.taskDetail.task_type === 'receive_task'
    }
  },
  filters: {
    priority (v) {
      switch (v) {
        case PRIORITY:
          return '高'
        case INTERMEDIATE:
          return '中'
        case ELEMENTARY:
          return '低'
        default:
          break
      }
    }
  },
  mounted () {
    this.id = this.$route.params.id
    this.getTaskDataDetail(this.id)
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/common';
.kanban_detail{
  .box{
    margin-top: 16 * @px1;
    background: #fff;
    overflow: hidden;
    .row{
      padding-left: 16 * @px1;
      padding-right: 16 * @px1;
      line-height: 56 * @px1;
      height: 56 * @px1;
    }
  }
  .first{
    .row{
      .right{
        text-align: right;
        color: @titleGray;
      }
    }
  }
  .second,.third{
    .row:last-child{
      color: @titleGray;
      line-height: 22 * @px1;
      padding-top: 17 * @px1;
      padding-bottom: 17 * @px1;
      height: 100%;
    }
  }
  .fourth{
    .trow{
      height: 46 * @px1;
      line-height: 46 * @px1;
      display: flex;
      .tcol{
        flex: 1;
        text-align: center;
        font-size: 14 * @px1;
      }
      .link{
        color: @linkColor;
      }
      .disabled{
        color: @gray;
      }
    }
    .odd{
      background: #fafafa;
    }
  }
  .fifth{
    .textarea_box{
      height: 100%;
      textarea{
        width: 100%;
        border: none;
        padding: 4 * @px1;
        font-size: 14 * @px1;
        margin-top: 10 * @px1;
      }
    }
  }
  .bottom_btn{
    margin-top: 36 * @px1;
  }
  .feedback_box{
    width: 80%;
    border: 1 * @px1 solid @gray;
    padding: 10 * @px1;
    position: absolute;
    top: 45%;
    transform: translateY(-50%);
    left: 10%;
    box-sizing: border-box;
    background: #fff;
    h3{
      color: #000;
      font-size: 18 * @px1;
      text-align: center;
      font-weight: 500;
    }
    .content{
      max-height: 300 * @px1;
      overflow-y: scroll;
      margin-bottom: 10 * @px1;
    }
  }
}
</style>
